"use client";
import React, { useState, useEffect } from "react";
// Mengasumsikan Sidebar dan toast tersedia atau ditangani secara eksternal di lingkungan pengguna
// import Sidebar from "../components/Sidebar"; // Dikomentari karena komponen Sidebar tidak disediakan dalam cuplikan
// import { toast } from "react-toastify"; // Dikomentari karena toast tidak disediakan dalam cuplikan
import axios from "axios";
// import { useRouter } from "next/navigation"; // Dikomentari karena useRouter tidak digunakan dalam logika cuplikan yang disediakan

// Placeholder untuk notifikasi toast jika tidak menggunakan react-toastify
const toast = {
  success: (message: string) => console.log("Sukses:", message),
  error: (message: string) => console.error("Error:", message),
};

// Mendefinisikan tipe untuk Alat dan Catatan Peminjaman
type Tool = {
  id: number;
  item_name: string;
  item_code: string;
  item_status: string;
  quantity: number;
  category: string;
};

type BorrowRecord = {
  id: number; // Ini akan dipetakan dari 'log_tools' dari API
  user_id: number; // Mungkin tidak ada di data API yang diberikan, akan diisi default
  user_name: string; // Ini akan dipetakan dari 'user' dari API
  tool_id: number; // Mungkin tidak ada di data API yang diberikan, akan diisi default
  tool_name: string; // Ini akan dipetakan dari 'item_name' dari API
  quantity: number;
  borrow_date: string; // Ini akan dipetakan dari 'date_borrow' dari API
  return_date: string | null; // Ini akan dipetakan dari 'date_return' dari API
  status: "borrowed" | "returned"; // Ini akan dipetakan dari 'tools_status' dari API
};

type BorrowFormData = {
  user_id: number;
  tool_id: number;
  quantity: number;
  borrow_date: string;
  return_date: string | null;
};

// Instans Axios untuk panggilan API
const api = axios.create({
  baseURL: "http://192.168.110.100:8080", // URL dasar untuk API
  timeout: 10000, // Batas waktu permintaan
});

// Interceptor Axios untuk menambahkan token Otorisasi ke permintaan
api.interceptors.request.use((config) => {
  const token = localStorage.getItem("token");
  if (token) {
    config.headers.Authorization = `Bearer ${token}`;
  }
  return config;
});

// Interceptor Axios untuk menangani respons 401 (Tidak Sah)
api.interceptors.response.use(
  (response) => response,
  (error) => {
    if (error.response?.status === 401) {
      localStorage.removeItem("token"); // Hapus token yang kedaluwarsa
      // window.location.href = "/login"; // Arahkan ke halaman login (dikomentari karena useRouter tidak digunakan)
      toast.error("Sesi kedaluwarsa. Silakan login kembali.");
    }
    return Promise.reject(error);
  }
);

// Komponen BorrowCard untuk menampilkan catatan peminjaman individu
function BorrowCard({
  record,
  onReturn,
}: {
  record: BorrowRecord;
  onReturn?: (id: number) => void;
}) {
  return (
    <div className="bg-white rounded-lg shadow-md overflow-hidden border border-gray-200">
      <div className="p-5">
        <div className="flex justify-between items-start mb-4">
          <div>
            <h2 className="text-lg font-semibold text-gray-800">
              {record.tool_name}
            </h2>
            <p className="text-sm text-gray-600">
              Dipinjam oleh: {record.user_name}
            </p>
          </div>
          <span
            className={`px-2 py-1 text-xs rounded-full ${
              record.status === "borrowed"
                ? "bg-yellow-100 text-yellow-800"
                : "bg-green-100 text-green-800"
            }`}
          >
            {record.status}
          </span>
        </div>

        <div className="grid grid-cols-2 gap-4 text-sm">
          <div>
            <p className="text-gray-500">Jumlah</p>
            <p className="font-medium">{record.quantity}</p>
          </div>
          <div>
            <p className="text-gray-500">Tanggal Peminjaman</p>
            <p className="font-medium">
              {new Date(record.borrow_date).toLocaleDateString()}
            </p>
          </div>
          <div>
            <p className="text-gray-500">Tanggal Pengembalian</p>
            <p className="font-medium">
              {record.return_date
                ? new Date(record.return_date).toLocaleDateString()
                : "Belum dikembalikan"}
            </p>
          </div>
        </div>
      </div>

      {record.status === "borrowed" && onReturn && (
        <div className="bg-gray-50 px-5 py-3 flex justify-end border-t border-gray-200">
          <button
            onClick={() => onReturn(record.id)}
            className="px-3 py-1.5 text-sm font-medium rounded-md text-white bg-green-600 hover:bg-green-700 transition-colors"
          >
            Tandai sebagai Dikembalikan
          </button>
        </div>
      )}
    </div>
  );
}

// Komponen BorrowFormModal untuk meminjam alat
function BorrowFormModal({
  tools,
  onBorrow,
  onCancel,
}: {
  tools: Tool[];
  onBorrow: (data: BorrowFormData) => void;
  onCancel: () => void;
}) {
  const [formData, setFormData] = useState<BorrowFormData>({
    user_id: 1, // ID pengguna default, ganti dengan ID pengguna terautentikasi yang sebenarnya
    tool_id: 0, // Inisialisasi dengan 0 atau ID alat valid default
    quantity: 1,
    borrow_date: new Date().toISOString().split("T")[0],
    return_date: null,
  });

  const [availableQuantities, setAvailableQuantities] = useState<{
    [key: number]: number;
  }>({});

  // Efek untuk menginisialisasi jumlah yang tersedia saat properti tools berubah
  useEffect(() => {
    const quantities = tools.reduce((acc, tool) => {
      acc[tool.id] = tool.quantity;
      return acc;
    }, {} as { [key: number]: number });
    setAvailableQuantities(quantities);
    // Tetapkan tool_id default jika alat tersedia dan formData.tool_id adalah 0
    if (tools.length > 0 && formData.tool_id === 0) {
      setFormData((prev) => ({ ...prev, tool_id: tools[0].id }));
    }
  }, [tools]); // Bergantung pada tools untuk menginisialisasi ulang jumlah jika daftar tools berubah

  // Tangani perubahan input formulir
  const handleChange = (
    e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement>
  ) => {
    const { name, value } = e.target;
    setFormData((prev) => ({
      ...prev,
      [name]: name === "quantity" ? parseInt(value) : value,
    }));
  };

  // Tangani pengiriman formulir
  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    onBorrow(formData);
  };

  // Temukan alat yang saat ini dipilih untuk menampilkan jumlah yang tersedia
  const selectedTool = tools.find((tool) => tool.id === formData.tool_id);

  return (
    <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50 p-4">
      <div className="bg-white rounded-xl shadow-2xl w-full max-w-md overflow-hidden">
        <div className="bg-blue-600 p-5 text-white">
          <h2 className="text-xl font-semibold">Pinjam Alat</h2>
        </div>

        <form onSubmit={handleSubmit} className="p-5 space-y-4">
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">
              Alat
            </label>
            <select
              name="tool_id"
              value={formData.tool_id}
              onChange={handleChange}
              className="w-full px-4 py-2 rounded-lg border border-gray-300 focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
              required
            >
              <option value="">Pilih Alat</option>
              {tools.map((tool) => (
                <option key={tool.id} value={tool.id}>
                  {tool.item_name} ({tool.item_code}) - Tersedia:{" "}
                  {availableQuantities[tool.id] || 0}
                </option>
              ))}
            </select>
          </div>

          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">
              Jumlah
            </label>
            <input
              type="number"
              name="quantity"
              min="1"
              max={selectedTool ? availableQuantities[selectedTool.id] : 1}
              value={formData.quantity}
              onChange={handleChange}
              className="w-full px-4 py-2 rounded-lg border border-gray-300 focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
              required
            />
            {selectedTool && (
              <p className="text-xs text-gray-500 mt-1">
                Tersedia: {availableQuantities[selectedTool.id] || 0}
              </p>
            )}
          </div>

          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">
              Tanggal Peminjaman
            </label>
            <input
              type="date"
              name="borrow_date"
              value={formData.borrow_date}
              onChange={handleChange}
              className="w-full px-4 py-2 rounded-lg border border-gray-300 focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
              required
            />
          </div>

          <div className="flex justify-end space-x-3 pt-4">
            <button
              type="button"
              onClick={onCancel}
              className="px-4 py-2 text-gray-700 font-medium rounded-lg border border-gray-300 hover:bg-gray-50"
            >
              Batal
            </button>
            <button
              type="submit"
              className="px-4 py-2 bg-blue-600 text-white font-medium rounded-lg hover:bg-blue-700"
            >
              Pinjam
            </button>
          </div>
        </form>
      </div>
    </div>
  );
}

// Komponen utama ToolBorrowingSystem
export default function log_tools() {
  const [tools, setTools] = useState<Tool[]>([]);
  const [borrowRecords, setBorrowRecords] = useState<BorrowRecord[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [showBorrowModal, setShowBorrowModal] = useState(false);
  const [search, setSearch] = useState("");
  const [currentPage, setCurrentPage] = useState(1);
  const [itemsPerPage] = useState(8);
  // const router = useRouter(); // Dikomentari karena useRouter tidak digunakan dalam logika cuplikan yang disediakan

  // Efek untuk mengambil data awal (alat dan catatan peminjaman)
  useEffect(() => {
    const fetchData = async () => {
      try {
        setLoading(true);
        setError(null);

        // Ambil data alat dari /data3
        const toolsRes = await api.get("/data3");
        setTools(toolsRes.data);

        // Ambil data catatan peminjaman dari /log_tools
        const recordsRes = await api.get("/log_tools");
        // Memetakan data log peminjaman dari API ke tipe BorrowRecord
        const mappedRecords: BorrowRecord[] = recordsRes.data.data.map((record: any) => ({
          id: record.log_tools,
          user_id: 0, // Tidak ada di data API yang diberikan, menggunakan default 0
          user_name: record.user,
          tool_id: 0, // Tidak ada di data API yang diberikan, menggunakan default 0
          tool_name: record.item_name,
          quantity: record.quantity,
          borrow_date: record.date_borrow,
          return_date: record.date_return,
          status: record.tools_status,
        }));
        setBorrowRecords(mappedRecords);

      } catch (err) {
        console.error("Kesalahan pengambilan data:", err);
        let errorMessage = "Gagal memuat data.";
        if (axios.isAxiosError(err) && err.code === 'ERR_NETWORK') {
          errorMessage = "Kesalahan Jaringan: Tidak dapat terhubung ke server API. Pastikan server backend berjalan di " + api.defaults.baseURL + " dan tidak ada masalah jaringan/CORS.";
        } else if (err instanceof Error) {
          errorMessage = err.message;
        }
        setError(errorMessage);
        toast.error(errorMessage);
      } finally {
        setLoading(false);
      }
    };

    fetchData();
  }, []); // Array dependensi kosong berarti efek ini berjalan sekali saat komponen dimuat

  // Tangani peminjaman alat
  const handleBorrowTool = async (data: BorrowFormData) => {
    try {
      // Temukan alat untuk memperbarui jumlahnya
      const toolToUpdate = tools.find((tool) => tool.id === data.tool_id);
      if (!toolToUpdate) {
        throw new Error("Alat tidak ditemukan");
      }

      // Periksa apakah jumlah yang cukup tersedia
      if (toolToUpdate.quantity < data.quantity) {
        throw new Error("Jumlah tidak cukup tersedia");
      }

      // Buat catatan peminjaman melalui API (POST ke /log_tools)
      const response = await api.post("/log_tools", data);

      if (response.data) {
        // Perbarui jumlah alat secara lokal dalam state
        const updatedTools = tools.map((tool) =>
          tool.id === data.tool_id
            ? { ...tool, quantity: tool.quantity - data.quantity }
            : tool
        );
        setTools(updatedTools);

        // Tambahkan catatan peminjaman baru ke state
        setBorrowRecords((prev) => [
          {
            id: response.data.log_tools, // Menggunakan log_tools sebagai ID dari respons API
            user_id: data.user_id,
            user_name: "Pengguna Saat Ini", // Ganti dengan nama pengguna sebenarnya dari konteks otentikasi
            tool_id: data.tool_id,
            tool_name: toolToUpdate.item_name,
            quantity: data.quantity,
            borrow_date: data.borrow_date,
            return_date: null,
            status: "borrowed",
          },
          ...prev, // Tambahkan catatan baru ke awal untuk visibilitas
        ]);

        setShowBorrowModal(false); // Tutup modal
        toast.success("Alat berhasil dipinjam");
      } else {
        throw new Error("Gagal meminjam alat: Tidak ada data dalam respons");
      }
    } catch (err) {
      console.error("Kesalahan saat meminjam alat:", err);
      toast.error(err instanceof Error ? err.message : "Gagal meminjam alat");
    }
  };

  // Tangani pengembalian alat
  const handleReturnTool = async (id: number) => {
    try {
      const recordToReturn = borrowRecords.find((record) => record.id === id);
      if (!recordToReturn) {
        throw new Error("Catatan tidak ditemukan");
      }

      // Perbarui status catatan peminjaman dan tanggal pengembalian melalui API (PUT ke /log_tools/{id})
      const response = await api.put(`/log_tools/${id}`, { // Menggunakan ID catatan untuk PUT
        return_date: new Date().toISOString().split("T")[0],
        status: "returned",
      });

      if (response.data) {
        // Perbarui jumlah alat secara lokal dalam state (tambahkan kembali jumlah yang dikembalikan)
        const updatedTools = tools.map((tool) =>
          tool.id === recordToReturn.tool_id
            ? { ...tool, quantity: tool.quantity + recordToReturn.quantity }
            : tool
        );
        setTools(updatedTools);

        // Perbarui catatan peminjaman dalam state
        setBorrowRecords((prev) =>
          prev.map((record) =>
            record.id === id
              ? {
                  ...record,
                  return_date: new Date().toISOString().split("T")[0],
                  status: "returned",
                }
              : record
          )
        );

        toast.success("Alat berhasil dikembalikan");
      } else {
        throw new Error("Gagal mengembalikan alat: Tidak ada data dalam respons");
      }
    } catch (err) {
      console.error("Kesalahan saat mengembalikan alat:", err);
      toast.error(err instanceof Error ? err.message : "Gagal mengembalikan alat");
    }
  };

  // Filter catatan peminjaman berdasarkan input pencarian
  const filteredRecords = borrowRecords.filter(
    (record) =>
      record.user_name.toLowerCase().includes(search.toLowerCase()) ||
      record.tool_name.toLowerCase().includes(search.toLowerCase()) ||
      record.status.toLowerCase().includes(search.toLowerCase())
  );

  // Logika paginasi
  const indexOfLastItem = currentPage * itemsPerPage;
  const indexOfFirstItem = indexOfLastItem - itemsPerPage;
  const currentItems = filteredRecords.slice(indexOfFirstItem, indexOfLastItem);
  const totalPages = Math.ceil(filteredRecords.length / itemsPerPage);

  // UI status memuat
  if (loading) {
    return (
      <div className="min-h-screen bg-gray-50 flex">
        {/* <Sidebar /> */} {/* Komponen Sidebar tidak disediakan */}
        <main className="flex-1 p-8 ml-64 flex items-center justify-center">
          <div className="flex flex-col items-center">
            <div className="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-blue-500 mb-4"></div>
            <div className="text-gray-600">Memuat data...</div>
          </div>
        </main>
      </div>
    );
  }

  // UI status kesalahan
  if (error) {
    return (
      <div className="min-h-screen bg-gray-50 flex">
        {/* <Sidebar /> */} {/* Komponen Sidebar tidak disediakan */}
        <main className="flex-1 p-8 ml-64 flex flex-col items-center justify-center">
          <div className="bg-white p-6 rounded-xl shadow-md max-w-md w-full text-center">
            <div className="text-red-500 mb-4">
              <svg
                className="w-16 h-16 mx-auto"
                fill="none"
                stroke="currentColor"
                viewBox="0 0 24 24"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth="2"
                  d="M12 8v4m0 4h.01M21 12a9 9 0 11-18 0 9 9 0 0118 0z"
                />
              </svg>
            </div>
            <h3 className="text-lg font-medium text-gray-900 mb-2">
              Terjadi Kesalahan
            </h3>
            <p className="text-gray-600 mb-6">{error}</p>
            <button
              onClick={() => window.location.reload()}
              className="px-4 py-2 bg-blue-600 text-white rounded-lg font-medium hover:bg-blue-700"
            >
              Coba Lagi
            </button>
          </div>
        </main>
      </div>
    );
  }

  // Konten utama Sistem Peminjaman Alat
  return (
    <div className="min-h-screen bg-gray-50 flex">
      {/* <Sidebar /> */} {/* Komponen Sidebar tidak disediakan */}
      <main className="flex-1 p-8 ml-64">
        <div className="flex flex-col md:flex-row md:items-center md:justify-between mb-8 gap-4">
          <div className="flex-1">
            <h1 className="text-3xl font-bold text-gray-800 mb-1">
              Sistem Peminjaman Alat
            </h1>
            <p className="text-gray-500">Kelola peminjaman dan pengembalian alat</p>
          </div>
          <div className="mb-4 md:mb-0 md:ml-4 flex justify-end">
            <button
              onClick={() => setShowBorrowModal(true)}
              className="px-5 py-2.5 bg-blue-600 text-white rounded-lg font-medium hover:bg-blue-700 flex items-center"
            >
              <svg
                className="w-5 h-5 mr-2"
                fill="none"
                stroke="currentColor"
                viewBox="0 0 24 24"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth="2"
                  d="M12 6v6m0 0v6m0-6h6m-6 0H6"
                />
              </svg>
              Pinjam Alat
            </button>
          </div>
        </div>

        <div className="mb-6">
          <div className="relative max-w-md">
            <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
              <svg
                className="w-5 h-5 text-gray-400"
                fill="none"
                stroke="currentColor"
                viewBox="0 0 24 24"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth="2"
                  d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z"
                />
              </svg>
            </div>
            <input
              type="text"
              placeholder="Cari catatan..."
              value={search}
              onChange={(e) => setSearch(e.target.value)}
              className="pl-10 pr-4 py-2.5 w-full rounded-lg border border-gray-300 focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
            />
          </div>
        </div>

        {filteredRecords.length === 0 ? (
          <div className="bg-white rounded-xl shadow-sm p-8 text-center">
            <svg
              className="w-16 h-16 mx-auto text-gray-400 mb-4"
              fill="none"
              stroke="currentColor"
              viewBox="0 0 24 24"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth="2"
                d="M9.172 16.172a4 4 0 015.656 0M9 10h.01M15 10h.01M21 12a9 9 0 11-18 0 9 9 0 0118 0z"
              />
            </svg>
            <h3 className="text-lg font-medium text-gray-900 mb-1">
              {borrowRecords.length === 0
                ? "Tidak ada catatan peminjaman ditemukan"
                : "Tidak ada catatan yang cocok ditemukan"}
            </h3>
            <p className="text-gray-500 mb-4">
              {borrowRecords.length === 0
                ? "Mulai dengan meminjam alat"
                : "Coba istilah pencarian yang berbeda"}
            </p>
            {borrowRecords.length === 0 && (
              <button
                onClick={() => setShowBorrowModal(true)}
                className="px-4 py-2 bg-blue-600 text-white rounded-lg font-medium hover:bg-blue-700"
              >
                Pinjam Alat Pertama
              </button>
            )}
          </div>
        ) : (
          <>
            <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-6">
              {currentItems.map((record) => (
                <BorrowCard
                  key={record.id}
                  record={record}
                  onReturn={
                    record.status === "borrowed" ? handleReturnTool : undefined
                  }
                />
              ))}
            </div>

            {totalPages > 1 && (
              <div className="flex justify-center mt-8">
                <nav className="inline-flex rounded-md shadow-sm -space-x-px">
                  <button
                    onClick={() =>
                      setCurrentPage((prev) => Math.max(1, prev - 1))
                    }
                    disabled={currentPage === 1}
                    className={`px-3 py-2 rounded-l-md border ${
                      currentPage === 1
                        ? "bg-gray-100 border-gray-300 text-gray-400 cursor-not-allowed"
                        : "bg-white border-gray-300 text-gray-500 hover:bg-gray-50"
                    }`}
                  >
                    Sebelumnya
                  </button>
                  {Array.from({ length: totalPages }, (_, i) => i + 1).map(
                    (number) => (
                      <button
                        key={number}
                        onClick={() => setCurrentPage(number)}
                        className={`px-4 py-2 border ${
                          currentPage === number
                            ? "bg-blue-50 border-blue-500 text-blue-600"
                            : "bg-white border-gray-300 text-gray-500 hover:bg-gray-50"
                        }`}
                      >
                        {number}
                      </button>
                    )
                  )}
                  <button
                    onClick={() =>
                      setCurrentPage((prev) => Math.min(totalPages, prev + 1))
                    }
                    disabled={currentPage === totalPages}
                    className={`px-3 py-2 rounded-r-md border ${
                      currentPage === totalPages
                        ? "bg-gray-100 border-gray-300 text-gray-400 cursor-not-allowed"
                        : "bg-white border-gray-300 text-gray-500 hover:bg-gray-50"
                    }`}
                  >
                    Berikutnya
                  </button>
                </nav>
              </div>
            )}
          </>
        )}

        {showBorrowModal && (
          <BorrowFormModal
            tools={tools.filter((tool) => tool.quantity > 0)} // Hanya tampilkan alat dengan jumlah > 0
            onBorrow={handleBorrowTool}
            onCancel={() => setShowBorrowModal(false)}
          />
        )}
      </main>
    </div>
  );
}
